import React, { Component } from 'react';
import './App.css';
import { Layout, Header, Navigation, Drawer, Content } from 'react-mdl';
import Main from './components/main';
import { Link } from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <div className="demo-big-content">
    <Layout>
        <Header className="header-color" title={<Link style={{textDecoration: 'none', color: '#2F2B0E', fontSize:'2em'}} to="/">josh luong</Link>} scroll>
            <Navigation>
                <Link  style = {{color: '#2F2B0E' , fontSize:'1.35em'}} to="/">Home</Link>
                <Link style = {{color: '#2F2B0E' , fontSize:'1.35em'}} to="/resume">Resume</Link>
                <Link  style = {{color: '#2F2B0E' , fontSize:'1.35em'}} to="/aboutme">About Me</Link>
                <Link  style = {{color: '#2F2B0E' , fontSize:'1.35em'}} to="/contact">Contact</Link>
            </Navigation>
        </Header>
        <Drawer style = {{color: '#2F2B0E'}} title={<Link style={{textDecoration: 'none', color: 'black'}} to="/">Josh Luong</Link>}>
            <Navigation>
              <Link to="/">Home</Link>
              <Link to="/resume">Resume</Link>
              <Link to="/aboutme">About Me</Link>
              <Link to="/contact">Contact</Link>
            </Navigation>
        </Drawer>
        <Content>
            <div className="page-content" />
            <Main/>
        </Content>
    </Layout>
</div>

    );
  }
}

export default App;


